@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color:azure;
}
/* Home.css */
.home-container {
  background-color: azure;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
}

.font-bold {
  font-weight: bold;
}

.text-center {
  text-align: center;
}

.profile-container {
  margin-top: 30px;
}

.profile-container p {
  color: black;
}

.profile-container h2 {
  font-style: italic;
}

.profile-image {
  border-radius: 60%;
  box-shadow: 0 4px 8px black;
}

.slide {
  display: none;
  width: 100%;
  text-align: center;
}

.slide img {
  width: 100%; 
  height: auto; 
}

h2 {
  color: black;
}

p {
  color: black;
  max-width: 600px;
  margin: 20px auto;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #2d3748;
  padding: 1rem;
  color: white;
  will-change: opacity, transform;
  text-align: center;
}

nav a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  transition: color 0.3s; 
  text-align: center;
  margin: 0 15px; 
}

nav a:hover {
  color: #a0aec0; 
  text-align: center;
}

nav a.active {
  color: #805ad5; 
  background-color: #f0f4f8; 
  border-radius: 4px; 
  padding: 0.5rem 1rem; 
}


button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: gray;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: black;
}

.bg-gray-800 {
  background-color: #2d3748;
}

.text-white {
  color: #fff;
}

.text-gray-300 {
  color: #a0aec0;
}

.bg-purple-500 {
  background-color: #805ad5;
}

.p-4 {
  padding: 1rem;
}

.space-x-4 > * {
  margin-right: 1rem;
}

.font-bold {
  font-weight: bold;
}



nav {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #333;
  color: white;
  padding: 1rem;
  text-align: center;
}

nav a {
  color: white;
  text-decoration: none;
  transition: color 0.3s, background-color 0.3s;
  margin: 0 15px;
