.skills-container {
  text-align: left;
  padding: 20px;
}

.skills-container h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.skill-item {
  margin-bottom: 20px;
}

.skill-item .skill-label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
}

.skill-item .progress-bar {
  background-color: #ddd;
  height: 20px;
  border-radius: 4px;
  overflow: hidden;
}

.skill-item .progress-bar .progress {
  height: 100%;
  background-color: cadetblue;
  width: var(--width); 
  transition: width 0.3s ease;
}
.skills-container h1 {
  font-style: italic;
  text-align: center;
}


.projects-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  
}


.project-item {
  background-color: cadetblue;
  border: 1px solid black;
  padding: 20px;
  margin: 30px;
  width: 400px;
  text-align: center;
}

.project-item h3 {
  color: #333;
}

.project-item p {
  color: black;
}

.project-item a {
  display: block;
  margin-top: 10px;
  text-decoration: none;
  color: brown;
}

.project-item a:hover {
  text-decoration: underline;
}

.slideshow-container {
  position: relative;
  max-width: 400px; 
  margin: auto;
}

.slideshow-container {
  position: relative;
  max-width: 400px; 
  margin: auto;
}

.slide {
  display: block;
  width: 100%; 
  text-align: center; 
}

.HomeImages {
  width: 100%; 
  height: 300px; 
  object-fit: cover; 
}

.prev,
.next {
  color: white;
  position: absolute;
  top: 50%;
  width: 30px; 
  margin-top: -15px; 
  font-size: 24px;
  cursor: pointer;
  user-select: none;
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}



.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms;
}

.fade-exit {
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 300ms;
}

.animate-bottom-enter {
  opacity: 0.01;
  transform: translateY(-20px);
}

.animate-bottom-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 300ms, transform 300ms;
}

.animate-bottom-exit {
  opacity: 1;
  transform: translateY(0);
}

.animate-bottom-exit-active {
  opacity: 0.01;
  transform: translateY(-20px);
  transition: opacity 300ms, transform 300ms;
}